.paragraf {

  position: absolute;
  left: 50%;
  top: -24px;
  transform: translate(-50%, 0);

  &__name {
    font-weight: 300;
    font-size: 48px;
    line-height: 48px;
    text-transform: uppercase;
    text-align: center;
  }

  &__name::before,
  &__name::after {
    content: "";
    display: block;
    height: 2px;
    background-color: $c-blue;
    width: calc(50% - 19.5px);
    margin-top: 5px;
  }

  &__name::before {
    position: absolute;
    right: 0;
    bottom: 0;
  }

  &__icon {
    position: absolute;
    left: 50%;
    height: 16px;
    width: 26px;
    transform: translate(-50%, 0);
  }
}

@media screen and (max-width: 820px) {
  .paragraf__name {
    font-size: 40px;
    line-height: 40px;
  }
}
