.consultation {
  padding: 149px 158px 145px 166px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  position: relative;
  background-color: $c-background-white;

  &__describe {
    width: 51%;
  }

  &__form {
    width: 41%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
  }

  &__question {
    font-weight: normal;
    font-size: 32px;
    line-height: 40px;
    max-width: 351px;
    margin: 0 0 24px;
  }
}

@media screen and (max-width: 820px) {
  .consultation {
    padding: 145px 80px;
    flex-direction: column;

    &__describe {
      width: 100%;
      text-align: center;
    }

    &__question {
      max-width: none;
    }

    &__form {
      width: 100%;
      max-width: 300px;
      margin: 40px auto 0;
    }
  }
}
